.App {
  text-align: center;
  font-family: var(--font);
}

.App-header {
  background-color: var(--bg-color);
  min-height: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  /* justify-content: center; */
}

section {
  width: 100%;
  text-align: -webkit-center;
}

.particle-js {
  width: 80%;
  height: 80%;
  position: absolute;
  background-size: cover;
}

/* Mobile View */

.nav {
    background-color: var(--bg-color);
    box-shadow: 0px 1px 50px rgba(0, 0, 0, 0.2);
}

.nav-items {
    color: var(--primary-color) !important;
}

.nav-brand {
    color: var(--primary-color) !important;
}

.nav-resume {
    background-color: transparent !important;
    color: var(--primary-color) !important;
    border: 1px solid var(--primary-color) !important;
}

.nav-items, .nav-brand, .nav-resume {
    margin: 15px 15px;
}

.nav-logo {
    height: auto;
}

/* Desktop View */

/* Hover */

.nav-items:hover {
    color: var(--secondary-color) !important;
    text-shadow: 0px 0px 4px var(--secondary-color);
}

.nav-resume:hover {
    color: var(--secondary-color) !important;
    text-shadow: 0px 0px 4px var(--secondary-color);
    box-shadow: 0px 0px 4px var(--secondary-color);
}

/* MOBILE VIEW */

.profile-row {
    margin-top: 5em;
    height: 40em;
    pointer-events: none;
}

.profile {
    width: 210px;
    height: 210px;
    border-radius: 50%;
    border: 5px solid var(--primary-color);
    box-shadow: 0px 15px 20px black;

}

.profile-img {
    width: auto;
    border-radius: 50%;
    height: 200px;
    box-shadow: 0 0 40px var(--primary-color);
}

.column-profile {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 2em;

}

.column-greeting {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    line-height: 0em;
}

.greeting-sub {
    color: var(--primary-color);
    font-size: 1.25em;
    text-align: center;
}

.greeting-name {
    color: white;
    font-size: 2.5em;
    text-align: center;
    line-height: 1.5em;
}

.column-status {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    line-height: 2em;
    justify-content: center;
    margin-top: 3em;
}

.lower-profile-text {
    color: white;
    font-size: 1em;
    padding: 0 1em;
}

.lower-profile-text-sub {
    color: var(--sub-text-color);
    font-size: 1em;
}

.profile-twitter,
.profile-linkedin,
.profile-github {
    color: var(--primary-color);
    font-size: 50px;
    text-align: center;
    transition: font-size 0.2s;
    pointer-events: all;
}

.profile-socials {
    display: none;
}

/* HORIZONTAL IPAD VIEW */

@media screen and (min-width: 1024px) {
    .profile-row {
        margin-top: 5em;
    }
    
    .profile {
        height: 210px;
        width: 210px;
    }
    
    .profile-img {
        height: 200px;
        width: 200px;
    }

    .column-greeting {
        text-align: left;  
        -webkit-animation: 0.75s ease-out 0s 1 slideInFromTop;  
                animation: 0.75s ease-out 0s 1 slideInFromTop;
    }
    
    .greeting-sub {
        font-size: 1em;
        text-align: right;
    }
    
    .greeting-name {
        font-size: 2.5em;
        text-align: right;
        line-height: 1.2em;
    }
    
    .column-status {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: center;
                justify-content: center;
        text-align: left;
        line-height: 2em;
        justify-content: center;
        margin-top: 0em;
        -webkit-animation: 0.75s ease-out 0s 1 slideInFromBottom;
                animation: 0.75s ease-out 0s 1 slideInFromBottom;
    }
    
    .lower-profile-text {
        font-size: 1.25em;
        padding: 0;
    }
    
    .lower-profile-text-sub {
        font-size: 1em;
    }

    .profile-twitter,
    .profile-linkedin,
    .profile-github {
        color: var(--primary-color);
        font-size: 50px;
        position: absolute;
        text-align: center;
        transition: font-size 0.2s;
    }

    .profile-twitter:hover,
    .profile-linkedin:hover,
    .profile-github:hover {
        color: var(--secondary-color);
        font-size: 55px;
        position: absolute;
    }

    /* .profile-twitter {
        left: 20%;
        bottom: -20%;
    } */

    .profile-github {
        left: 35%;
        bottom: -30%;
    }

    .profile-linkedin {
        right: 35%;
        bottom: -30%;
    }

    .profile-socials {
        display: contents;
    }
}

/* 720p LAPTOP VIEW */

@media screen and (min-width: 1280px) {
    .profile {
        height: 310px;
        width: 310px;
    }
    
    .profile-img {
        height: 300px;
        width: 300px;
    }

    .greeting-sub {
        font-size: 1.2em;
    }
    
    .greeting-name {
        font-size: 2.5em;
        line-height: 1.2em;
    }

    .profile-twitter,
    .profile-linkedin,
    .profile-github {
        font-size: 50px;
    }

    .profile-twitter:hover,
    .profile-linkedin:hover,
    .profile-github:hover {
        font-size: 55px;

    }

    /* .profile-twitter {
        left: 25%;
        bottom: -15%;
    } */

    .profile-github {
        left: 35%;
        bottom: -25%;
    }

    .profile-linkedin {
        right: 35%;
        bottom: -25%;
    }
    
    .column-status {
        line-height: 2em;
    }
    
    .lower-profile-text {
        font-size: 1.5em;
        padding-right: 10px;
    }
    
    .lower-profile-text-sub {
        font-size: 1.25em;
    }
}

/* DESKTOP VIEW */

@media screen and (min-width: 1920px) {
    .profile-row {
        margin-top: 10em;
    }
    
    .profile {
        height: 420px;
        width: 420px;
        border: 10px solid var(--primary-color);
    }
    
    .profile-img {
        height: 400px;
        width: 400px;
        box-shadow: 0 0 40px var(--primary-color);
    }

    .column-profile {
        margin-top: 0em;
        position: relative;
    }

    .profile-twitter,
    .profile-linkedin,
    .profile-github {
        font-size: 50px;
    }

    .profile-twitter:hover,
    .profile-linkedin:hover,
    .profile-github:hover {
        font-size: 55px;

    }

    /* .profile-twitter {
        left: 25%;
        bottom: -10%;
    } */

    .profile-github {
        left: 35%;
        bottom: -20%;
    }

    .profile-linkedin {
        right: 35%;
        bottom: -20%;
    }
    
    .greeting-sub {
        font-size: 1.5em;
    }
    
    .greeting-name {
        font-size: 3em;
        line-height: 1.2em;
    }
    
    .column-status {
        line-height: 2em;
    }
    
    .lower-profile-text {
        font-size: 1.5em;
        padding-right: 5em;
        
    }
    
    .lower-profile-text-sub {
        font-size: 1.2em;
    }
}

@-webkit-keyframes slideInFromTop {
    0% {
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        opacity: 0%;
        display: none;
    }
    70%{
        display: block;
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 100%;
    }
}

@keyframes slideInFromTop {
    0% {
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        opacity: 0%;
        display: none;
    }
    70%{
        display: block;
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 100%;
    }
}

@-webkit-keyframes slideInFromBottom {
    0% {
        -webkit-transform: translateY(50%);
                transform: translateY(50%);
        opacity: 0%;
        display: none;
    }
    70%{
        display: block;
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 100%;
    }
}

@keyframes slideInFromBottom {
    0% {
        -webkit-transform: translateY(50%);
                transform: translateY(50%);
        opacity: 0%;
        display: none;
    }
    70%{
        display: block;
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 100%;
    }
}




.aboutme-container {
    margin-top: 5em;
    height: 600px;
    width: 100%;
    background-color: var(--bg-over);
    color: var(--text-color);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 0 5%;
    font-size: 1em;
    white-space: pre-line;
}

.aboutme-heading {
    color: white;
}

.sub-quote,
.text-highlight {
    margin-top: 2em;
    color: var(--primary-color);
}


/* HORIZONTAL IPAD VIEW */

@media screen and (min-width: 1024px) {
    .aboutme-container {
        font-size: 1.2em;
        padding: 0 20%;
        height: 500px;
    }
}

/* 720p LAPTOP VIEW */

@media screen and (min-width: 1280px) {
}

/* DESKTOP VIEW */

@media screen and (min-width: 1920px) {
    .aboutme-container {
        font-size: 1.2em;
        padding: 0 20%;
        height: 400px;
    }
}





.portfolio-container {
    margin-top: 5em;
}

.portfolio-heading {
    color: white;
    margin-bottom: 1em;
}

.portfolio-card {
    min-height: 500px;
    max-width: 380px;
    padding: 1em 1em;
    -webkit-justify-content: center;
            justify-content: center;
}

/* Card Information */

.card-info {
    height: 100%;
    width: 100%;
    border: 2px solid var(--primary-color);
    background-color: var(--bg-over);
    border-radius: 0.8em;
    box-shadow: 0 0 5px var(--primary-color);
    color: var(--text-color);
    padding: 1em;
}

.card-title {
    padding-top: 1em;
    padding-left: 2em;
    text-align: left;
    font-weight: bold;
    font-size: 1em;
    overflow-x: hidden;
}

.card-external-url,
.card-external-url:hover,
.card-external-url:active,
.card-external-url:visited {
    padding-top: 1em;
    padding-right: 2em;
    text-align: right;
}

.card-btn,
.card-btn:active,
.card-btn:visited,
.card-btn:hover {
    margin-left: 2em;
    background-color: transparent;
    color: var(--primary-color);
    border: 1px solid var(--primary-color);
}

.card-btn:hover {
    color: var(--secondary-color);
    text-shadow: 0px 0px 4px var(--secondary-color);
    box-shadow: 0px 0px 4px var(--secondary-color);
}

.card-img {
    padding: 0 2em;
}

.card-desc {
    height: 180px;
    padding: 1em 2.5em;
    font-size: 0.8em;
    text-align: left;
    overflow-y: auto;
}

.text-highlight {
    color: var(--primary-color);
}

.experience-container {
    margin-top: 5em;
    height: auto;
    width: 100%;
    background-color: var(--bg-over);
    color: var(--text-color);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: 1em;
}

.experience-heading {
    color: white;
    margin-top: 1.5em;
}

.experience-section {
    margin-top: 5em;
}

.company {
    margin-bottom: 5em;
}

.companyLogo {
    height: 10em;
    max-width: 10em;
    object-fit: scale-down;
}

.companyDetails {
    text-align: center;
    line-height: 1em;
}

.companyName {
    font-size: 1.5em;
    color: var(--secondary-color);
    font-weight: bold;
}

.companyLocation {
    font-size: 1em;
    color: var(--sub-text-color);
}

/* Roles */

.role, .role-time, .role-desc {
    line-height: 1.5em;
}

.role {
    font-size: 1.1em;
    color: var(--primary-color);
}

.role-time {
    color: var(--sub-text-color);
}

.role-desc {
    color: var(--text-color);
}

@media screen and (max-width: 768px) {
    .company {
        text-align-last: left;
    }

    .companyLogo {
        margin-bottom: 1em;
    }
}

@media screen and (min-width: 577px) {
    .companyDetails {
        text-align: left;
    }
}

@media screen and (min-width: 1281px) {
    .experience-section {
        padding: 0 20em;
    }
}

.skills-section {
    margin: 5em 0;
}

.skills-heading {
    color: white;
    margin-bottom: 1em;
}

.skills-panel {
    line-height: 1.75em;
    text-align: center;
